import { SpottedPage } from './../pages/spotted/spotted';
import { PerfilPage } from './../pages/perfil/perfil';
import { HomePage } from './../pages/home/home';
import { WelcomePage } from './../pages/welcome/welcome';
import { FirebaseAuthState } from 'angularfire2';
import { AuthServiceProvider } from './../providers/auth/auth.service';
import { User } from './../models/user.model';
import { Component, ViewChild } from '@angular/core';
import { Nav, Platform } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { UserServiceProvider } from '../providers/user/user.service';
import firebase from 'firebase';
import {Deeplinks} from "@ionic-native/deeplinks";
import { ChatPage } from '../pages/chat/chat';
import { SpottedDetalhesPage } from '../pages/spotted-detalhes/spotted-detalhes';



@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;
  currentUser :User;
  rootPage: any = 'welcome';

  pages: Array<{title: string, component: any}>;

  constructor(deeplinks:Deeplinks,
    authService:AuthServiceProvider,userService:UserServiceProvider,public platform: Platform, public statusBar: StatusBar, public splashScreen: SplashScreen) {
   authService.auth.subscribe(
     (authState:FirebaseAuthState) =>{
       if(authState){
         userService.currentUser.subscribe(
           (user:User) =>{
             this.currentUser = user;
           }
         );
       }
     }
   );
   this.platform.ready().then(() => {
  
    // Okay, so the platform is ready and our plugins are available.
    // Here you can do any higher level native things you might need.
    this.statusBar.styleDefault();
    this.splashScreen.hide();
  
    deeplinks.route({
      '/welcome':'WelcomePage',
      '/home' :'HomePage',
      '/chats': 'ChatPage',
      '/perfil':'PerfilPage',
      '/spotted':'SpottedPage',
      '/spotted-detalhes':'SpottedDetalhesPage'
    }).subscribe((match) => {
      // match.$route - the route we matched, which is the matched entry from the arguments to route()
      // match.$args - the args passed in the link
      // match.$link - the full link data
      console.log('Successfully matched route', match);
      console.log(match.$route);
      this.nav.setRoot(match.$route, match.$args);
    }, (nomatch) => {
      // nomatch.$link - the full link data
      console.error('Got a deeplink that didn\'t match', nomatch);
    });

    /*
deeplinks.routeWithNavController(this.nav,{
  '/welcome':WelcomePage,
  '/home/:chave' :HomePage,
  '/chats/:recipient.$key': ChatPage,
  '/perfil':PerfilPage,
  '/spotted':SpottedPage,
  '/spotted-detalhes':SpottedDetalhesPage
}).subscribe((match) => {
  // match.$route - the route we matched, which is the matched entry from the arguments to route()
  // match.$args - the args passed in the link
  // match.$link - the full link data
//  this.nav.setRoot(match.$route, match.$args);
  console.log('Native route', match);
}, (nomatch) => {
  // nomatch.$link - the full link data
  console.log('Got a deeplink that didn\'t match', nomatch);
});
*/
    /* const firebaseConfig = {
      apiKey: "AIzaSyDBQl4HG_lqovYTg-ATndA3Co4JnUdoyiM",
      authDomain: "spottedagrvai.firebaseapp.com",
      databaseURL: "https://spottedagrvai.firebaseio.com",
      projectId: "spottedagrvai",
      storageBucket: "spottedagrvai.appspot.com",
      messagingSenderId: "832356265521"
    };
    firebase.initializeApp(firebaseConfig);
    */
  });

    // used for an example of ngFor and navigation
    this.pages = [
      { title: 'Home', component: 'HomePage' }
    ];

  }

  initializeApp() {

  }

  openPage(page) {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
    this.nav.setRoot(page.component);
  }
}



